import React, { useState, useEffect } from "react";
import Spinner from "../Common/Spinner";
import { useStatisticsSectionContext } from "../../Context/StatisticsSectionContext";

const DashboardCheckbox = () => {
  const { data, loading } = useStatisticsSectionContext();
  const [selectedOptions, setSelectedOptions] = useState({});
  const [selectedYear, setSelectedYear] = useState(new Date().getFullYear());

  // Initialize selected options based on data
  useEffect(() => {
    if (data) {
      const initialOptions = data.reduce((acc, fuel) => {
        acc[fuel.name] = true; // Set all fuel types to true initially
        return acc;
      }, {});
      setSelectedOptions(initialOptions);
    }
  }, [data]);

  if (loading) return <Spinner />;

  const handleCheckboxChange = (e) => {
    const { name, checked } = e.target;
    setSelectedOptions((prev) => ({ ...prev, [name]: checked }));
  };

  const handleYearChange = (e) => {
    setSelectedYear(e.target.value);
  };

  return (
    <div className="container mx-auto p-4">
      <h1>Vyberte si palivo</h1>
      <form className="w-full  max-h-full group cursor-default select-none py-2 pl-3 pr-9 text-gray-900 data-[focus]:bg-indigo-600 data-[focus]:text-white">
        <div className="z-10 mt-1 max-h-56 w-full overflow-auto rounded bg-white py-1 text-base shadow-lg ring-1 ring-black ring-opacity-5 focus:outline-none data-[closed]:data-[leave]:opacity-0 data-[leave]:transition data-[leave]:duration-100 data-[leave]:ease-in sm:text-sm">
          {data.map((fuel, index) => {
            return (
              <label
                key={fuel.name}
                className="flex justify-between hover:bg-neutral-200  ml-3 block truncate font-normal group-data-[selected]:font-semibold flex-row-reverse items-center space-x-3"
              >
                <input
                  type="checkbox"
                  name={fuel.name}
                  checked={selectedOptions[fuel.name] || false}
                  onChange={handleCheckboxChange}
                  key={fuel.name}
                  className="flex h-4 w-4 text-blue-600 border-gray-300 rounded focus:ring-blue-500"
                />
                <span className="flex">{fuel.name}</span>
              </label>
            );
          })}
        </div>
        <div className="flex ">
          <label className="block text-gray-700 font-bold mb-2">
            Select Year:
          </label>
          <input
            type="number"
            min="2000"
            max={new Date().getFullYear()}
            value={selectedYear}
            onChange={handleYearChange}
            className="w-full px-4 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring focus:ring-blue-500"
          />
        </div>
        <button
          type="submit"
          className="bg-sky-500 hover:bg-sky-700 rounded px-2 my-2"
        >
          Search
        </button>
      </form>
    </div>
  );
};

export default DashboardCheckbox;
